import React from "react";
import {Link} from "react-router-dom"
import Horizon from "./pages/Horizon"
import * as d3 from "d3"

interface Index3Props{
    theme: any,
    Themes: any
}
interface Index3State{
    id_list: string[]
}

// 导入数据
let result_path = require("../../data/result2_1.csv")

function Index3 (props: Index3Props, state: Index3State){
    let {theme, Themes} = props
    let [id_list, setId_list] = React.useState([])
    let HorizonTheme = Themes.TargetVueTheme
    // 获取id数据
    React.useEffect(() => {
        d3.csv(result_path).then(value => {
            // console.log(value)
            let list_set = new Set()
            for(let i = 0; i < value.length; i++){
                list_set.add(value[i]['id'])
            } 
            setId_list(Array.from(list_set as any))
        })
    }, [])
    React.useEffect(() => {
    }, [id_list])
    let horizon_list = id_list.map((v, index) => {
        return(
            <Horizon index={index} key={v + index} id={v} theme={Themes.HorizonTheme}></Horizon>
        )
    })

    // let horizon_list: any = [<div></div>]
    // if(id_list.length !== 0){
    //     horizon_list[0] = <Horizon index={2} id={id_list[2]} theme={Themes.HorizonTheme}></Horizon>
    // }
    return(
        <div className="Index3" style={{position: 'absolute', ...theme}}>
            {/* 转换按钮 */}
            <div style={{float: 'left', width: theme.width, height: 30, marginTop: 5}}>
                <button style={{float: 'left', fontSize: '10px', fontWeight: 600, width: 120, height: 30, marginLeft: 10}}>
                    <Link to="/index1">实验一:MAE折线图</Link>
                </button>
                <button style={{float: 'left', fontSize: '10px', fontWeight: 600, width: 120, height: 30, marginLeft: 10}}>
                    <Link to="/">实验三:地平线图</Link>
                </button>
            </div>
            {/* 下面的折线图 */}
            <div style={{float: "left", ...HorizonTheme, marginTop: 5, marginLeft: 10, overflow: 'auto'}}>
                {horizon_list}
            </div>
        </div>
    )
}

export default Index3;