import React from "react";
import * as d3 from "d3"
// import { index } from "d3";
import "../css/p.css"
import { hrtime } from "process";
import $ from 'jquery'

interface Dataset{
    date: string,
    id: string,
    result: string,
    dValue: number,
    dValue_abs: number // 差值的绝对值
}

interface HorizonProps{
    theme: any,
    id: string,
    index: number
}
interface HorizonState{
    dataset: Dataset[],
    num_layer: number,
    date_list_: string[] // 时间列表
}

// 获取所需数据
let dValue_path = require("../../../data/3_各层原始指数差值数据.csv")
let result_path = require("../../../data/result2_1.csv")

function Horizon (props: HorizonProps, state: HorizonState){
    let {theme, id, index} = props
    let [dataset, setDataset] = React.useState([])
    let [date_list_, setDate_list_] = React.useState([])
    // 设置层数
    let [num_layer, setNum_layer] = React.useState(3)
    let colors_list: {down: String[], up: String[]} = {
        down: ["#BFBFFF", "#8F8FFF", "#6B6BFF"],
        up: ["#FFD78F", "#FFC050", "#FFA300"]
    }
    React.useEffect(() => {
        if(id !== undefined && dataset.length === 0){
            d3.csv(dValue_path).then(dValue => {
                d3.csv(result_path).then(result => {
                    // 获取dValue_id
                    let dValue_id:{[key: string]: any} = {}
                    for(let i = 0; i < dValue.length; i++){
                        if(dValue[i]['id'] === id){
                            dValue_id[dValue[i]['date'] as any] = dValue[i]
                        }
                    }
                    // 获取dataset
                    let dataset_: Dataset[] = []
                    let dateList_: string[] = []
                    // 获取result_id
                    let result_id: any[] = []
                    for(let i = 0; i < result.length; i++){
                        if(result[i].id === id){
                            let item: Dataset = {
                                date: result[i].date as any,
                                id: id,
                                result: result[i]['result'] as any,
                                dValue: Number(dValue_id[result[i].date as any].dValue),
                                dValue_abs: Math.abs(Number(dValue_id[result[i].date as any].dValue))
                            }
                            dateList_.push(result[i].date as any)
                            dataset_.push(item)
                        }
                    }
                    // 数据排序
                    dataset_.sort(function(a, b){
                        return (new Date(a['date']) as any) - (new Date(b['date']) as any)
                    })
                    setDate_list_(dateList_ as any)
                    setDataset(dataset_ as any)
                })
            })
        }
    })
    React.useEffect(() => {
        // console.log(dataset)
        if(dataset.length > 0){
            // 绘制地平线图
            drawHorizon(1)
            // 绘制时间滑动条
            drawTimeSlide()
        }
    }, [dataset])
    // 绘制地平线图
    function drawHorizon(rate: number){
        let width = theme.width
        let height = theme.height - 45
        let margin = {left: 50, right: 50, top: 10, button: 20}
        d3.select("#horizon" + id).selectAll('g').remove()
        let svg = d3.select("#horizon" + id)
        .append('g')
        .attr('transform', `translate(${margin.left}, ${margin.top})`)
        // 找数据的最大最小值
        let max_dValue: number = Number(d3.max(dataset, function(d){
            return d['dValue_abs'];
        }))
        // 设置地平线图的间隔值
        let value_item = max_dValue / num_layer
        // 时间列表
        let date_list: Array<string> = []
        for(let i = 0; i < dataset.length; i++){
            date_list.push(dataset[i]['date'])
        }
        // 设置比例尺
        let xScale = d3.scaleBand()
        // .domain([0, dataset.length - 1])
        .domain(date_list)
        .range([0, (width - margin.left - margin.right) / rate])
        
        let yScale = d3.scaleLinear()
        .domain([0, value_item])
        .range([height - margin.top - margin.button, 0])
        // 绘制直线(x坐标轴)
        let xAxis = d3.axisBottom(xScale)
        .tickValues([dataset[0]['date'], dataset[dataset.length - 1]['date']])
        let xAxis_g = svg.append('g')
        .attr('transform', `translate(0, ${height - margin.top - margin.button})`)
        .call(xAxis)
        // xAxis_g.selectAll('text')
        // .text(d => {
        //     return dataset[d as any]['date']
        // })
        // 绘制折线
        for(let i = -num_layer; i < num_layer; i++){
            let begin_spot: Dataset = dataset[0]
            begin_spot['dValue_abs'] = 0
            let end_spot: Dataset = dataset[dataset.length - 1]
            end_spot['dValue_abs'] = 0
            let index = i >= 0 ? i : -i - 1
            // console.log(index)
            // 处理出绘制折线所需数据
            let dataset_line: Dataset[] = $.extend(true, [], dataset)
            for(let j = 0; j < dataset_line.length; j++){
                if((dataset_line[j].dValue >= 0 && i >= 0) || (dataset_line[j].dValue < 0 && i < 0)){
                    if(dataset_line[j].dValue_abs < (index * value_item)){
                        dataset_line[j].dValue_abs = 0
                    } else if(dataset_line[j].dValue_abs > ((index + 1) * value_item)){
                        dataset_line[j].dValue_abs = value_item
                    } else {
                        dataset_line[j].dValue_abs -= index * value_item
                    }
                } else {
                    dataset_line[j].dValue_abs = 0
                }
                // dataset_line[j].dValue_abs = Math.abs(dataset_line[j].dValue)
            }
            dataset_line = [begin_spot, ...dataset_line, end_spot, begin_spot]
            // console.log(dataset_line)
            // 创建绘制折线的path生成器
            let line_make = d3.line()
            .x((d, i) => xScale(dataset_line[i]['date']) as any)
            .y((d, i) => yScale(dataset_line[i]['dValue_abs']))
            // 设置折线颜色
            let color = i >= 0 ? colors_list.up[i] : colors_list.down[index]
            // 绘制折线
            let line_g = svg.append('path')
            .datum(dataset_line)
            .attr('d', line_make as any)
            .attr('stroke', 'none')
            .attr('fill', color as any)
            .attr('stroke-width', 1)
            // break
        }
    }
    // 绘制时间滑动条
    function drawTimeSlide(){
        // console.log(date_list_)
        
        let width_horizon = theme.width
        let height_horizon = theme.height - 45
        let width = theme.width - 100
        let height = 15
        let margin_horizon = {left: 50, right: 50, top: 10, button: 20}
        // 设置滑动的矩形的参数
        // 矩形左侧离最左侧的距离
        let x_begin = 0
        // 矩形宽度
        let rect_width = width
        d3.select("#slide" + id).selectAll('g').remove()
        // 绘制滑动条
        let svg = d3.select("#slide" + id)
        .append('g')
        .attr('transform', `translate(0, 0)`)
        // 拖动矩形时，确定矩形初始位置用
        let rect_bet_begin = -1
        // 拖动事件
        let drag_rect: any = d3.drag()
        .on('start', function(){
            d3.select(this).raise() // 开始拖拽
        })
        .on('drag', function(event, d){
            let x = event.x;
            if(rect_bet_begin === -1){
                rect_bet_begin = x - line_position[0]
            } else if(x - rect_bet_begin >= 0 && x + (rect_width - rect_bet_begin) <= width) {
                line_position[0] = x - rect_bet_begin
                line_position[1] = line_position[0] + rect_width
                d3.select(this)
                .attr('x', line_position[0])
                d3.select("#slide_line" + id + '0')
                .attr('x1', line_position[0])
                .attr('x2', line_position[0])
                d3.select("#slide_line" + id + '1')
                .attr('x1', line_position[1])
                .attr('x2', line_position[1])
                let rate = (line_position[1] - line_position[0]) / width
                d3.select('#horizon' + id)
                .attr('viewBox', [line_position[0] / width * (width_horizon - margin_horizon.left - margin_horizon.right) / rate, 0, width_horizon, height_horizon ])
            }
        })
        .on('end', function(){
            rect_bet_begin = -1
        })
        // 滑动g(滑动条主体)
        let slide_g = svg.append('g')
        .attr('id', 'slide_g' + id)
        .attr('transform', `translate(0, 0)`)
        // 绘制滑动条(矩形)
        let rect_slide = slide_g.append('rect')
        .attr('x', 0)
        .attr('y', 0)
        .attr('rx', 5)
        .attr('ry', 5)
        .attr('width', rect_width)
        .attr('height', height)
        .attr('fill', 'rgb(180, 180, 180)')
        .attr('cursor', 'move')
        .call(drag_rect)
        // 设置滑动条左右两侧的位置
        let line_position = [0, rect_width]
        // 滑动条两边线的拖拽事件
        let line_drag = d3.drag()
        .on('start', function(){
            d3.select(this).raise() // 开始拖拽
        })
        .on('drag', function(event){
            let judge = +d3.select(this).attr('value')
            let x = event.x
            if(x >= 0 && x <= width){
                if(judge === 0 && x <= line_position[1]){
                    line_position[0] = x
                    rect_width = line_position[1] - line_position[0]
                    d3.select('#slide' + id).select('rect')
                    .attr('x', x)
                    .attr('width', rect_width)
                } else if(judge === 1 && x >= line_position[0]) {
                    line_position[1] = x
                    rect_width = line_position[1] - line_position[0]
                    d3.select('#slide' + id).select('rect')
                    .attr('width', rect_width)
                }
                d3.select(this)
                .attr('x1', x)
                .attr('x2', x)
            }
        })
        .on('end', function(){
            // 当前展示的部分占整体的比例
            let rate = (line_position[1] - line_position[0]) / width
            // 重绘地平线图
            drawHorizon(rate)
            d3.select('#horizon' + id)
            .attr('viewBox', [line_position[0] / width * (width_horizon - margin_horizon.left - margin_horizon.right) / rate, 0, width_horizon, height_horizon ])
        })
        // 绘制滑动条两边的线(方便缩小滑动条)
        let line_data = [
            [[0, 0], [0, 15]],
            [[rect_width, 0], [rect_width, 15]]
        ]
        let line_slide = slide_g.selectAll("line")
        .data(line_data)
        .join('line')
        .attr('id', (d, i) => 'slide_line' + id + i)
        .attr('value', (d, i) => i)
        .attr('x1', d => d[0][0])
        .attr('x2', d => d[1][0])
        .attr('y1', d => d[0][1])
        .attr('y2', d => d[1][1])
        .attr('fill', 'none')
        .attr('stroke', 'white')
        .attr('opacity', 0.1)
        .attr('stroke-width', 4)
        .attr('cursor', (d, i) => i === 0 ? 'w-resize' : 'e-resize')
        .call(line_drag as any)
    }
    return(
        <div className="Horizon" style={{position: 'relative', float: "left", ...theme, borderTop: index !== 0 ? "1px solid rgb(180, 180, 180)" : "none"}}>
            <div style={{float: 'left', width: theme.width, height:index !== 0 ? 19 : 20, fontSize: "12px", fontWeight: '600', lineHeight: index !== 0 ? '19px' : '20px'}}>
                {id}
            </div>
            {/* 上层地平线图 */}
            <div style={{float: 'left', width: theme.width, height: theme.height - 45, background: 'white'}}>
                <svg id={"horizon" + id} style={{float: 'left', width: theme.width, height: theme.height - 45}}></svg>
            </div>
            {/* 下层滑动条图 */}
            <div style={{float: 'left', width: theme.width - 98, height: 17, marginTop: 4, marginLeft: 49, borderRadius: '5px', background: 'white', border: '1px solid rgb(180, 180, 180)'}}>
                <svg id={"slide" + id} style={{float: 'left', width: theme.width - 100, height: 15,}}></svg>
            </div>
        </div>
    )
}

export default Horizon;